body {
  background: lightgray; }

#mainPart h2 {
  margin-top: 25vh;
  text-align: center;
  color: navy;
  font-size: 5em;
  font-family: 'Titan One', cursive;
  font-style: italic;
  text-shadow: 0 1px 0 #0040ff, 0 2px 0 #0080ff, 0 3px 0 #00bfff, 0 4px 0 #00ffff; }

#mainPart h3 {
  font-size: 1.5em;
  text-align: center;
  font-style: italic;
  font-family: 'Fredoka One', cursive;
  text-shadow: 0 1px 0 #484848, 0 2px 0 #606060, 0 3px 0 #787878, 0 4px 0 #9f9f9f; }

.medievalTextTitle {
  font-family: 'MedievalSharp', cursive;
  text-align: center; }

.gameTitle {
  color: green;
  font-weight: bold;
  font-size: 7em;
  margin-top: 22vh;
  font-style: italic;
  text-shadow: 0 1px 0 #66FF00, 0 2px 0 #99FF33, 0 3px 0 #CCFF66, 0 4px 0 #FFFF99; }

.subGameTitle {
  font-weight: bold;
  font-size: 2.5em;
  font-style: italic;
  text-shadow: 0 1px 0 #484848, 0 2px 0 #606060, 0 3px 0 #787878, 0 4px 0 #9f9f9f; }

#titleGameHeader {
  width: 100%;
  font-family: 'MedievalSharp', cursive;
  text-align: center;
  color: green;
  font-weight: bold;
  font-size: 1.7em;
  font-style: italic;
  text-shadow: 0 1px 0 #66FF00, 0 2px 0 #99FF33, 0 3px 0 #CCFF66, 0 4px 0 #FFFF99; }

#subTitleGameHeader {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-style: italic;
  color: black;
  font-size: 0.7em;
  text-shadow: 0 1px 0 lightgray, 0 2px 0 #fff; }

.btnInfo {
  background: linear-gradient(#FFF, #FF8C00);
  font-family: 'MedievalSharp', cursive;
  box-shadow: 0 1px 0 #606060, 0 2px 0 #606060, 0 3px 0 #838383, 0 4px 0 #838383, 0 5px 0 #a6a6a6, 0 5px 0 #a6a6a6, 0 6px 0 #ededed, 0 7px 0 #ededed; }

.btnLicence {
  background: linear-gradient(#FFF, #8A2BE2);
  font-family: 'MedievalSharp', cursive;
  box-shadow: 0 1px 0 #606060, 0 2px 0 #606060, 0 3px 0 #838383, 0 4px 0 #838383, 0 5px 0 #a6a6a6, 0 5px 0 #a6a6a6, 0 6px 0 #ededed, 0 7px 0 #ededed; }

.btnTutorial {
  background: linear-gradient(#FFF, #BDB76B);
  font-family: 'MedievalSharp', cursive;
  margin-top: 1.75vh;
  box-shadow: 0 1px 0 #606060, 0 2px 0 #606060, 0 3px 0 #838383, 0 4px 0 #838383, 0 5px 0 #a6a6a6, 0 5px 0 #a6a6a6, 0 6px 0 #ededed, 0 7px 0 #ededed; }

.btnNewGame {
  background: linear-gradient(#FFF, green);
  font-family: 'MedievalSharp', cursive;
  margin-top: 1.75vh;
  box-shadow: 0 1px 0 #606060, 0 2px 0 #606060, 0 3px 0 #838383, 0 4px 0 #838383, 0 5px 0 #a6a6a6, 0 5px 0 #a6a6a6, 0 6px 0 #ededed, 0 7px 0 #ededed; }

.btnInfo:hover, .btnLicence:hover, .btnTutorial:hover, .btnNewGame:hover {
  color: white;
  text-shadow: 1px 1px black; }

.btnInfo:active, .btnLicence:active, .btnTutorial:active, .btnNewGame:active, #name:active, #race:active, #occupation:active, #features:active, #features2:active, #equipment:active, #skills:active, #infoCreator:active, #acceptName:active, #startGame:active {
  transform: translate(0, 4px);
  box-shadow: 0 1px 0 #a6a6a6, 0 2px 0 #a6a6a6, 0 3px 0 #ededed, 0 4px 0 #ededed; }

.btnInfo:focus, .btnLicence:focus, .btnTutorial:focus, .btnNewGame:focus, #acceptName:focus {
  outline: none; }

* {
  margin: 0;
  padding: 0; }

html, body {
  width: 100%;
  height: 100%; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

header, footer {
  height: 5vh; }

nav, #mainPart, #alerts {
  height: 78vh; }

.grid-display {
  width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.flexForBtns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: flex-start; }

.basicBtn {
  width: 40%;
  height: 7%;
  font-weight: bold;
  font-size: 1.05em;
  text-shadow: 1px 1px white; }

.medievalText {
  font-family: 'MedievalSharp', cursive; }

.basicText {
  text-indent: 3%;
  font-weight: bold;
  margin-top: 2%;
  font-size: 1.3em;
  text-shadow: 1px 1px white; }

.center {
  text-align: center; }

.width20 {
  width: 20%; }

.width24 {
  width: 24%; }

.width33 {
  width: 33%; }

.width49 {
  width: 49%; }

.width100 {
  width: 100%; }

.blueText {
  color: blue; }

.greenText {
  color: green; }

.fontSize08em {
  font-size: 0.8em; }

.fontSize09em {
  font-size: 0.9em; }

.fontSize1em {
  font-size: 1em; }

.fontSize11em {
  font-size: 1.1em; }

.fontSize12em {
  font-size: 1.2em; }

.fontSize13em {
  font-size: 1.3em; }

.textUnderlineGold {
  text-decoration: underline;
  text-decoration-color: gold; }

.marginTop5 {
  margin-top: 5%; }

.marginTop4vh {
  margin-top: 4vh; }

.paddingUpDown1 {
  padding: 1% 0 1% 0; }
